body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card-slide-warpper {
  width: 100%;
  touch-action: none;
  overflow: hidden; }

.card-wapper {
  display: flex; }
  .card-wapper.back-transition {
    transition: all 0.4s; }
  .card-wapper .card-button-list {
    display: flex; }
    .card-wapper .card-button-list button {
      height: 100%;
      width: 5em;
      background: skyblue;
      color: #ffffff;
      outline: none;
      border: none; }

