.card-slide {
  display: flex;
  width: 100%;
  flex: 0 0 100%;
  align-items: center;
  padding: 0 20px;
  box-sizing: border-box; }
  .card-slide img {
    width: 50px;
    height: 50px;
    flex: 0 0 50px;
    margin-right: 20px; }
  .card-slide .card-right {
    flex: 1 1; }
    .card-slide .card-right p {
      margin: 8px 0;
      padding: 0; }
  .card-slide .item {
    display: flex;
    justify-content: space-between; }

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.5); }

.dialog-page button {
  font-size: 40px; }

